import React, { useEffect, useState } from "react";
import second_popup_screen from "../assets/image/popup_2.png";
import Clear from "../assets/image/Clear.png";
import { useNavigate } from "react-router-dom";
// import { View, Button } from 'react-native';
// import { WebView } from 'react-native-webview';
import styled from "styled-components";
import axios from "axios";

const ModalContainer = styled.div`
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 100;
`;

const ModalContent = styled.div`
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
`;

const StartButton = styled.button`
  position: absolute;
  width: 100%;
  max-width: 350px;
  height: 58px;
  bottom: 20%;
  background: #ffffff;
  box-shadow: 0px 0px 10px rgba(0, 28, 145, 0.2);
  border-radius: 8px;
  cursor: pointer;
  text-align: center;
  font-weight: bold;
  letter-spacing: 0.5em;
  font-size: 18px;
  color: #6a8bff;

  @media (max-width: 720px) {
    font-size: 1em;
    width: 80%;
    max-width: 350px;
    height: 58px;
    bottom: 20%;
  }

  @media (max-height: 600px) {
    font-size: 1em;
    height: 58px;
    bottom: 20%;
  }

  @media (max-aspect-ratio: 4/3) {
    font-size: 1em;
    height: 58px;
    bottom: 20%;
  }
`;

const PopupImage2 = styled.img`
  width: 100%;
  height: 100%;
  object-fit: cover;
`;

const CloseButton = styled.img`
  position: absolute;
  top: 3%;
  right: 3%;
  width: 5%;
  max-width: 26px;
  height: auto;
  cursor: pointer;
  z-index: 2;

  @media (max-width: 720px) {
    display: none;
  }
`;

const TextBox = styled.div`
  position: absolute;
  bottom: 10%;
  font-size: 0.6em;
`;

function Popup() {
  const [userId, setUserId] = useState("");
  const [jwt, setJwt] = useState("");
  const [showModal, setShowModal] = useState(false);
  const [rnSend, setRnSend] = useState(false);
  const navigate = useNavigate();

  useEffect(() => {
    setShowModal(true);
    window.ReactNativeWebView?.postMessage(JSON.stringify(true));
    //android
    document.addEventListener("message", (e) => {
      try {
        const parseData = JSON.parse(e.data);
        setUserId(parseData.id);
        setJwt(parseData.jwt);
        setRnSend(true);
      } catch (err) {
        console.log(err);
      }
    });
    //ios
    window.addEventListener("message", (e) => {
      try {
        const parseData = JSON.parse(e.data);
        setUserId(parseData.id);
        setJwt(parseData.jwt);
        setRnSend(true);
      } catch (err) {
        console.log(err);
      }
    });
  }, []);
  useEffect(() => {
    const surbayCheck = async () => {
      await axios({
        method: "post",
        url: `${process.env.REACT_APP_API_URL}/surbey/check`,
        headers: {
          Authorization: jwt,
        },
      })
        .then((res) => {
          console.log(res.data);
          if (res.data.message === 0) {
            navigate("/result", {
              state: {
                jwt: jwt,
                userId: userId,
              },
            });
          }
        })
        .catch((err) => {
          console.log(err);
        });
    };
    if (rnSend) {
      surbayCheck();
    }
  }, [rnSend]);

  const handleCloseModal = () => {
    setShowModal(false); // Close the modal
    navigate("/");
  };

  const handleStartSurvey = () => {
    // Logic for navigating to the survey start page
    setShowModal(false);
    navigate("/question", {
      state: {
        jwt: jwt,
        userId: userId,
      },
    });
  };

  return (
    <>
      {showModal && (
        <ModalContainer>
          <ModalContent>
            <PopupImage2 src={second_popup_screen} alt="Modal Image" />
            <CloseButton onClick={handleCloseModal} src={Clear} />
            <StartButton onClick={handleStartSurvey}>시작하기</StartButton>
            <TextBox>여행지 제공 : TourAPI4.0</TextBox>
          </ModalContent>
        </ModalContainer>
      )}
    </>
  );
}

export default Popup;
